body {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  color: #fff; }

.is-dark {
  background-color: #F62459;
  background-image: linear-gradient(rgba(25, 181, 254, 0.6), rgba(246, 36, 89, 0.6)), linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('https://images.unsplash.com/photo-1471462730965-4535e7a5375f?dpr=1&auto=compress,format&fit=crop&w=1600&h=&q=80&cs=tinysrgb&crop='); }

.hero.is-dark {
  .nav-item a:not(.button), a.nav-item {
    color: #fff;
    padding: 0 30px; } }

.nav-item {
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
  .button {
    font-weight: bold;
    text-transform: uppercase;
    color: #fff;
    background-color: transparent;
    border: 2px solid #fff;
    padding: 10px 20px;
    height: 45px; } }

.promo-img {
  border-radius: 10px;
  border: 10px solid #fff; }

.marginize {
  margin-bottom: 1.5rem; }

.marginize-top {
  margin-top: 1.5rem; }
